import { MoreVertical } from "lucide-react";

export default function UsersPage() {
  const users = [
    { id: 1, name: "Abdelrahman Bodi", email: "bodi@example.com", role: "Admin", status: "Active" },
    { id: 2, name: "Omar Ali", email: "omar@example.com", role: "Customer", status: "Suspended" },
    { id: 3, name: "Sara Mahmoud", email: "sara@example.com", role: "Customer", status: "Active" },
  ];

  return (
    <div className="p-8 flex-1">

      {/* HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Users</h1>
          <p className="text-gray-500 mt-1">Manage all users in the Threadix platform</p>
        </div>

        <button className="px-5 py-2.5 bg-black text-white font-medium rounded-xl hover:bg-gray-900 transition">
          + Add User
        </button>
      </div>

      {/* FILTERS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search users by name or email..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />

        <div className="flex gap-3">
          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Roles</option>
            <option>Admin</option>
            <option>Customer</option>
          </select>

          <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
            <option value="">All Status</option>
            <option>Active</option>
            <option>Suspended</option>
          </select>
        </div>
      </div>

      {/* USERS TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-hidden">
        <table className="w-full min-w-[600px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">User</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Email</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Role</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {users.map((u) => (
              <tr key={u.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4">
                  <div className="flex flex-col">
                    <span className="font-semibold text-gray-900">{u.name}</span>
                    <span className="text-sm text-gray-500">ID: {u.id}</span>
                  </div>
                </td>

                <td className="px-6 py-4 text-gray-700">{u.email}</td>

                <td className="px-6 py-4">
                  <span className="px-2.5 py-1 text-xs font-medium bg-gray-200 rounded-lg">{u.role}</span>
                </td>

                <td className="px-6 py-4">
                  <span className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                    u.status === "Active"
                      ? "bg-green-100 text-green-700"
                      : "bg-red-100 text-red-700"
                  }`}>
                    {u.status}
                  </span>
                </td>

                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

    </div>
  );
}
